import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from 'react-redux'
import {Form, Row, Col, Container} from 'react-bootstrap'
import { getPostsSummaryAPI, createPostAPI } from "../../apis/post";
import '../../assets/styles/post/PostCreate.css'

function PostCreate() {
  // data
  let [title, setTitle] = useState("")
  let [category, setCategory] = useState("")
  let [contents, setContents] = useState("")
  let [validationMsg, setValidationMsg] = useState("")
  const account = useSelector(state => state.account);
  let navigate = useNavigate()

  // functions
  let onChangeTitle = (e) => {
    setTitle(e.target.value)
  }
  let onChangeCategory = (e) => {
    setCategory(e.target.value)
  }
  let onChangeContents = (e) => {
    setContents(e.target.value)
  }
  let isValidate = () => {
      if (title.length < 2 || title.length > 30) {
          setValidationMsg(" 2  30  .")
      }
      else if (!category.length) {
          setValidationMsg(" .")
      }
      else if (category.length > 10) {
          setValidationMsg("  .")
      }
      else if (contents.length < 10) {
          setValidationMsg("  . 10  .")
      }
      else if (contents.length > 1000)
          setValidationMsg("  .")
      else {
          setValidationMsg("")
          return true
      }
      return false
  }

  let createPost = () => {
      if(isValidate()) {
        let contentsTransformed = contents.replace(/(?:\r\n|\r|\n)/g, '<br />') //   
        createPostAPI(account.id, title, category, contentsTransformed)
          .then((data) => {
            let id = data.data.postId
            navigate(
              `/detail/${id}`,
              {
                state: {
                  post: data.data
                },
                replace: true
              }
            )
          })
          .catch((err) => {
            console.log(err)
            setValidationMsg(" .    .")
          })
      }
  }

  // views
  return (
    <div className="PostCreate">
      <h2 className="post-create-title title"> </h2>
      <Container className="panel">
        <Form>
          <Form.Group as={Row} className="mb-3">
            <Col sm>
              <Form.Label></Form.Label>
              <Form.Control type="text" placeholder=" " onChange={onChangeTitle} />
            </Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Col sm>
              <Form.Label></Form.Label>
              <Form.Control type="text" placeholder=" " onChange={onChangeCategory} />
            </Col>
          </Form.Group>
          <Form.Group as={Row} className="mb-3">
            <Col sm>
              <Form.Label></Form.Label>
              {/* <Form.Control type="textarea" placeholder="Password" /> */}
              <Form.Control as="textarea" aria-label="With textarea" id="post-create-content-intpu" placeholder=" " onChange={onChangeContents} />
            </Col>
          </Form.Group>
          {
                validationMsg.length >= 1
                ? <div className="validation-msg">{ validationMsg }</div>
                : <span></span>
          }
          <br/>
          <button className="btn" id="main-btn-lg" type="button" onClick={createPost}></button>
        </Form>
      </Container>
    </div>
  );
}

export default PostCreate;